import { useRef, useEffect } from 'react';
import { MessageBubble } from './MessageBubble';
import { useConnection } from '../context/ConnectionContext';
import { Loader2 } from 'lucide-react';

export function MessageList() {
  const { messages, connectionState } = useConnection();
  const messagesEndRef = useRef<HTMLDivElement>(null);
  const containerRef = useRef<HTMLDivElement>(null);

  // Auto-scroll to bottom
  useEffect(() => {
    if (messagesEndRef.current) {
      messagesEndRef.current.scrollIntoView({ behavior: 'smooth' });
    }
  }, [messages]);

  const isThinking = connectionState.status === 'connected' && 
    messages.length > 0 && 
    messages[messages.length - 1].role === 'user' &&
    messages[messages.length - 1].status === 'sent';

  return (
    <div 
      ref={containerRef}
      className="flex-1 overflow-y-auto scrollbar-thin p-4 space-y-4"
    >
      {messages.length === 0 ? (
        <div className="flex h-full flex-col items-center justify-center text-center">
          <div className="mb-4 flex h-16 w-16 items-center justify-center rounded-full bg-openclaw-100">
            <span className="text-3xl"></span>
          </div>
          <h3 className="text-lg font-semibold text-gray-800">Welcome to OpenClaw</h3>
          <p className="mt-2 max-w-sm text-sm text-gray-500">
            Start a conversation with your AI assistant. Type a message below to begin.
          </p>
        </div>
      ) : (
        <>
          {messages.map((message) => (
            <MessageBubble 
              key={message.id} 
              message={message}
            />
          ))}
          
          {/* Thinking indicator */}
          {isThinking && (
            <div className="flex items-center gap-2 text-gray-400 animate-fade-in">
              <Loader2 className="h-4 w-4 animate-spin" />
              <span className="text-xs">AI is thinking...</span>
            </div>
          )}
          
          <div ref={messagesEndRef} />
        </>
      )}
    </div>
  );
}
