import { useConnection } from '../context/ConnectionContext';
import { cn } from '../utils';
import { 
  Wifi, 
  WifiOff, 
  Loader2, 
  AlertCircle,
  Settings,
  LogOut
} from 'lucide-react';

interface ChatHeaderProps {
  onOpenSettings: () => void;
}

export function ChatHeader({ onOpenSettings }: ChatHeaderProps) {
  const { connectionState, disconnect } = useConnection();

  const getStatusIcon = () => {
    switch (connectionState.status) {
      case 'connected':
        return <Wifi className="h-4 w-4 text-green-500" />;
      case 'connecting':
        return <Loader2 className="h-4 w-4 animate-spin text-yellow-500" />;
      case 'error':
        return <AlertCircle className="h-4 w-4 text-red-500" />;
      default:
        return <WifiOff className="h-4 w-4 text-gray-400" />;
    }
  };

  const getStatusText = () => {
    switch (connectionState.status) {
      case 'connected':
        return 'Connected';
      case 'connecting':
        return 'Connecting...';
      case 'error':
        return connectionState.error || 'Connection error';
      default:
        return 'Disconnected';
    }
  };

  // Pairing status UI - show guidance if device isn't paired yet
  const deviceToken = (typeof window !== 'undefined') ? localStorage.getItem('openclaw_device_token') : null;
  const isPaired = Boolean(deviceToken);
  const isConnected = connectionState.status === 'connected';

  const copyPairingCommands = async () => {
    const text = 'openclaw devices list\nopenclaw devices approve <requestId>';
    try {
      await navigator.clipboard.writeText(text);
      console.log('[WebChat] Pairing commands copied to clipboard');
    } catch {
      // ignore
    }
  };

  return (
    <div className="flex flex-col border-b border-gray-200 bg-white">
      <header className="px-4 py-3">
        <div className="mx-auto flex max-w-4xl items-center justify-between">
          <div className="flex items-center gap-3">
            <div className="flex h-10 w-10 items-center justify-center rounded-xl bg-gradient-to-br from-openclaw-500 to-openclaw-700 text-white shadow-sm">
              <span className="text-lg"></span>
            </div>
            <div>
              <h1 className="text-lg font-semibold text-gray-900">OpenClaw</h1>
              <div className="flex items-center gap-1.5">
                {getStatusIcon()}
                <span className={cn(
                  "text-xs font-medium",
                  connectionState.status === 'connected' ? 'text-green-600' :
                  connectionState.status === 'error' ? 'text-red-600' :
                  'text-gray-500'
                )}>
                  {getStatusText()}
                </span>
              </div>
            </div>
          </div>

          <div className="flex items-center gap-2">
            <button
              onClick={onOpenSettings}
              className="rounded-lg p-2 text-gray-500 hover:bg-gray-100 hover:text-gray-700 transition-colors"
              title="Connection Settings"
            >
              <Settings className="h-5 w-5" />
            </button>
            <button
              onClick={disconnect}
              className="rounded-lg p-2 text-gray-500 hover:bg-red-50 hover:text-red-600 transition-colors"
              title="Disconnect"
            >
              <LogOut className="h-5 w-5" />
            </button>
          </div>
        </div>
      </header>

      {/* Pairing hint panel (visible when connected but not yet paired/authorized) */}
      {isConnected && !isPaired && (
        <div className="bg-amber-50 border-t border-amber-200 px-4 py-3 animate-slide-down">
          <div className="mx-auto max-w-4xl">
            <div className="flex items-start gap-3">
              <AlertCircle className="h-5 w-5 text-amber-600 shrink-0 mt-0.5" />
              <div className="flex-1">
                <h3 className="text-sm font-semibold text-amber-900"> (Authorization Required)</h3>
                <p className="text-xs text-amber-800 mt-1 leading-relaxed">
                  
                </p>
                <div className="mt-2 flex flex-col sm:flex-row gap-2">
                  <div className="flex-1 bg-white/50 border border-amber-200 rounded px-2 py-1.5 font-mono text-[11px] text-amber-900">
                    openclaw devices approve &lt;requestId&gt;
                  </div>
                  <button 
                    onClick={copyPairingCommands}
                    className="whitespace-nowrap rounded bg-amber-600 px-3 py-1.5 text-xs font-medium text-white hover:bg-amber-700 transition-colors"
                  >
                    
                  </button>
                </div>
                <p className="text-[10px] text-amber-700 mt-2">
                   <code>openclaw devices list</code>  ID
                </p>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
