// Lightweight Gateway Docs Reader
// Reads OpenClaw Gateway docs and provides a tiny search API for the UI/skills

export class GatewayDocsReader {
  private docsMd: string | null = null;
  private lastFetch: number | null = null;

  // Fetch docs from the official URL
  async fetchDocs(url: string = '/docs-proxy/gateway'): Promise<string> {
    try {
      // In a real environment, we would use a proper HTTP client with error handling and retry logic
      const res = await fetch(url, { cache: 'no-store' });
      if (!res.ok) throw new Error(`Failed to fetch docs: ${res.status}`);
      this.docsMd = await res.text();
      this.lastFetch = Date.now();
      return this.docsMd;
    } catch (err) {
      console.warn('Failed to fetch docs via proxy, trying direct URL:', err);
      
      // If proxy fails (e.g. in production), try direct URL as fallback
      if (url === '/docs-proxy/gateway') {
        try {
          const directRes = await fetch('https://docs.openclaw.ai/gateway', { cache: 'no-store' });
          if (directRes.ok) {
            this.docsMd = await directRes.text();
            this.lastFetch = Date.now();
            return this.docsMd;
          }
        } catch (directErr) {
          console.error('Direct fetch also failed:', directErr);
        }
      }

      // Final fallback: Use a friendly error message as the "docs"
      this.docsMd = `# OpenClaw Gateway Documentation\n\n>  Unable to load latest documentation from https://docs.openclaw.ai/gateway due to connection or CORS issues.\n\n### Troubleshooting\n1. Check your internet connection.\n2. Ensure the OpenClaw documentation site is accessible.\n3. If you are using an SSH tunnel, ensure it is configured to allow these requests.\n\n### Quick Links\n- [Official Documentation](https://docs.openclaw.ai/gateway)\n- [GitHub Repository](https://github.com/openclaw/openclaw)\n`;
      return this.docsMd;
    }
  }

  getDocs(): string | null {
    return this.docsMd;
  }

  isExpired(): boolean {
    if (!this.lastFetch) return true;
    const oneHour = 60 * 60 * 1000;
    return Date.now() - this.lastFetch > oneHour;
  }

  // Improved search in the markdown for a query string
  search(query: string): string[] {
    if (!this.docsMd) return [];
    const lower = query.toLowerCase();
    const lines = this.docsMd.split('\n');
    const results: string[] = [];

    for (let i = 0; i < lines.length; i++) {
      if (lines[i].toLowerCase().includes(lower)) {
        // Get context (2 lines before and after)
        const start = Math.max(0, i - 2);
        const end = Math.min(lines.length, i + 3);
        const context = lines.slice(start, end).join('\n');
        results.push(context);
      }
    }
    return results.slice(0, 5);
  }

  // Get specific section content
  getSection(sectionTitle: string): string | null {
    if (!this.docsMd) return null;

    const lines = this.docsMd.split('\n');
    let inSection = false;
    const sectionLines: string[] = [];

    for (const line of lines) {
      if (line.startsWith('##') && line.toLowerCase().includes(sectionTitle.toLowerCase())) {
        inSection = true;
        continue;
      }

      if (inSection) {
        if (line.startsWith('##') && !line.toLowerCase().includes(sectionTitle.toLowerCase())) {
          break;
        }
        sectionLines.push(line);
      }
    }

    return sectionLines.length > 0 ? sectionLines.join('\n') : null;
  }
}

export const gatewayDocsReader = new GatewayDocsReader();
