import React, { useState, useEffect } from 'react';
import { useConnection } from '../context/ConnectionContext';
import type { GatewayConfig } from '../types';
import { X, Eye, EyeOff, Loader2, AlertCircle } from 'lucide-react';
import { cn } from '../utils';

interface ConnectionModalProps {
  isOpen: boolean;
  onClose: () => void;
}

export function ConnectionModal({ isOpen, onClose }: ConnectionModalProps) {
  const { connect, isConnecting } = useConnection();
  const [url, setUrl] = useState('');
  const [token, setToken] = useState('');
  const [showToken, setShowToken] = useState(false);
  const [error, setError] = useState('');

  useEffect(() => {
    if (isOpen) {
      // Load saved config
      const saved = localStorage.getItem('openclaw_config');
      if (saved) {
        try {
          const config: GatewayConfig = JSON.parse(saved);
          let savedUrl = config.url || '';
          //  localhost  127.0.0.1
          if (savedUrl.includes('localhost')) {
            savedUrl = savedUrl.replace('localhost', '127.0.0.1');
          }
          setUrl(savedUrl);
          setToken(config.token || '');
        } catch {
          // Invalid saved config
        }
      } else {
        // Default values for local development
        setUrl('ws://127.0.0.1:18789');
        setToken('');
      }
      setError('');
    }
  }, [isOpen]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');

    if (!url.trim()) {
      setError('Gateway URL is required');
      return;
    }

    // Validate URL format
    let wsUrl = url.trim();
    if (!wsUrl.startsWith('ws://') && !wsUrl.startsWith('wss://')) {
      // Try to convert http to ws
      if (wsUrl.startsWith('http://')) {
        wsUrl = wsUrl.replace('http://', 'ws://');
      } else if (wsUrl.startsWith('https://')) {
        wsUrl = wsUrl.replace('https://', 'wss://');
      } else {
        //  ws IP
        const isLocal = wsUrl.includes('localhost') || wsUrl.includes('127.0.0.1') || wsUrl.startsWith(':');
        wsUrl = isLocal ? `ws://${wsUrl}` : `ws://${wsUrl}`; //  ws
      }
    }

    //  SSH  IP  127.0.0.1  localhost
    if (wsUrl.includes('localhost')) {
      wsUrl = wsUrl.replace('localhost', '127.0.0.1');
    }

    try {
      await connect({
        url: wsUrl,
        token: token.trim() || undefined
      });
      onClose();
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to connect');
    }
  };

  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50 p-4">
      <div className="w-full max-w-md animate-fade-in rounded-2xl bg-white p-6 shadow-xl">
        <div className="mb-6 flex items-center justify-between">
          <div>
            <h2 className="text-xl font-semibold text-gray-900">Connect to Gateway</h2>
            <p className="mt-1 text-sm text-gray-500">
              Enter your OpenClaw Gateway details
            </p>
          </div>
          <button
            onClick={onClose}
            className="rounded-lg p-2 text-gray-400 hover:bg-gray-100 hover:text-gray-600"
          >
            <X className="h-5 w-5" />
          </button>
        </div>

        <form onSubmit={handleSubmit} className="space-y-4">
          <div>
            <label className="mb-1.5 block text-sm font-medium text-gray-700">
              Gateway URL
            </label>
            <input
              type="text"
              value={url}
              onChange={(e) => setUrl(e.target.value)}
              placeholder="ws://localhost:18789"
              className="w-full rounded-lg border border-gray-300 px-3 py-2 text-sm placeholder:text-gray-400 focus:border-primary-500 focus:outline-none focus:ring-2 focus:ring-primary-500/20"
            />
            <p className="mt-1 text-xs text-gray-500">
              Example: ws://127.0.0.1:18789 or wss://your-server:18789
            </p>
          </div>

          <div>
            <label className="mb-1.5 block text-sm font-medium text-gray-700">
              Auth Token (optional)
            </label>
            <div className="relative">
              <input
                type={showToken ? 'text' : 'password'}
                value={token}
                onChange={(e) => setToken(e.target.value)}
                placeholder="Enter your gateway token"
                className="w-full rounded-lg border border-gray-300 px-3 py-2 pr-10 text-sm placeholder:text-gray-400 focus:border-primary-500 focus:outline-none focus:ring-2 focus:ring-primary-500/20"
              />
              <button
                type="button"
                onClick={() => setShowToken(!showToken)}
                className="absolute right-3 top-1/2 -translate-y-1/2 text-gray-400 hover:text-gray-600"
              >
                {showToken ? (
                  <EyeOff className="h-4 w-4" />
                ) : (
                  <Eye className="h-4 w-4" />
                )}
              </button>
            </div>
            <p className="mt-1 text-xs text-gray-500">
              Token from OPENCLAW_GATEWAY_TOKEN or config
            </p>
          </div>

          {error && (
            <div className="space-y-3">
              <div className="flex items-start gap-2 rounded-lg bg-red-50 p-3 text-sm text-red-700">
                <AlertCircle className="mt-0.5 h-4 w-4 shrink-0" />
                <div className="flex-1">
                  <p className="font-medium">Connection Failed</p>
                  <p className="mt-1 opacity-90">{error}</p>
                </div>
              </div>

              {(url.includes('localhost') || url.includes('127.0.0.1')) && (
                <div className="rounded-lg border border-amber-100 bg-amber-50 p-3 text-xs text-amber-800">
                  <p className="font-semibold mb-2"> (Tunnel Troubleshooting):</p>
                  <ul className="list-disc pl-4 space-y-1">
                    <li><strong></strong> <code>127.0.0.1</code>  <code>localhost</code></li>
                    <li><strong>SSH </strong> <code>netstat -ano | findstr :18789</code>  LISTENING  ()</li>
                    <li><strong></strong> <code>ssh 112-local</code>  <code>ssh 112-vpn</code> </li>
                  </ul>
                </div>
              )}
            </div>
          )}

          <div className="flex gap-3 pt-2">
            <button
              type="button"
              onClick={onClose}
              className="btn-secondary flex-1"
              disabled={isConnecting}
            >
              Cancel
            </button>
            <button
              type="submit"
              disabled={isConnecting}
              className={cn(
                "btn-primary flex-1",
                isConnecting && "opacity-70"
              )}
            >
              {isConnecting ? (
                <>
                  <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                  Connecting...
                </>
              ) : (
                'Connect'
              )}
            </button>
          </div>
        </form>

        <div className="mt-6 rounded-lg bg-gray-50 p-4 text-xs text-gray-600">
          <p className="font-medium text-gray-700 mb-2">Quick Tips:</p>
          <ul className="list-disc list-inside space-y-1">
            <li>Local: <code className="bg-gray-200 px-1 rounded">ws://127.0.0.1:18789</code></li>
            <li>Tailscale: <code className="bg-gray-200 px-1 rounded">wss://machine.tailnet.ts.net:18789</code></li>
            <li>First connection requires device approval</li>
          </ul>
        </div>
      </div>
    </div>
  );
}
