import { cn, formatTimestamp } from '../utils';
import type { Message } from '../types';
import { User, Bot, AlertCircle } from 'lucide-react';

interface MessageBubbleProps {
  message: Message;
}

export function MessageBubble({ message }: MessageBubbleProps) {
  const isUser = message.role === 'user';
  const isError = message.status === 'error';

  return (
    <div
      className={cn(
        'flex w-full animate-fade-in',
        isUser ? 'justify-end' : 'justify-start'
      )}
    >
      <div
        className={cn(
          'flex max-w-[85%] gap-3',
          isUser ? 'flex-row-reverse' : 'flex-row'
        )}
      >
        {/* Avatar */}
        <div
          className={cn(
            'flex h-8 w-8 shrink-0 items-center justify-center rounded-full',
            isUser ? 'bg-primary-500' : 'bg-gray-200'
          )}
        >
          {isUser ? (
            <User className="h-4 w-4 text-white" />
          ) : (
            <Bot className="h-4 w-4 text-gray-600" />
          )}
        </div>

        {/* Message Content */}
        <div className={cn('flex flex-col', isUser ? 'items-end' : 'items-start')}>
          <div
            className={cn(
              'message-bubble relative',
              isUser ? 'message-bubble-user' : 'message-bubble-assistant',
              isError && 'bg-red-50 border-red-200 text-red-800'
            )}
          >
            {isError && (
              <div className="flex items-center gap-1.5 mb-1 text-red-600">
                <AlertCircle className="h-3.5 w-3.5" />
                <span className="text-xs font-medium">Failed to send</span>
              </div>
            )}
            <div className="whitespace-pre-wrap">
              {typeof message.content === 'string' 
                ? message.content 
                : JSON.stringify(message.content)}
            </div>
            
            {/* Status indicator for user messages */}
            {isUser && message.status === 'sending' && (
              <span className="absolute -bottom-5 right-0 text-[10px] text-gray-400">
                Sending...
              </span>
            )}
          </div>
          
          {/* Timestamp */}
          <span className="mt-1 text-[10px] text-gray-400">
            {formatTimestamp(message.timestamp)}
          </span>
        </div>
      </div>
    </div>
  );
}
