import React, { useState, useRef, useEffect } from 'react';
import { Send, Loader2, Square } from 'lucide-react';
import { cn } from '../utils';
import type { ConnectionState } from '../types';
import { getClient } from '../client';

interface ChatInputProps {
  onSend: (message: string) => Promise<void>;
  isSending: boolean;
  connectionState: ConnectionState;
}

export function ChatInput({ onSend, isSending, connectionState }: ChatInputProps) {
  const [input, setInput] = useState('');
  const textareaRef = useRef<HTMLTextAreaElement>(null);
  const isConnected = connectionState.status === 'connected';
  const isThinking = isConnected && isSending;
  
  // Get current session key safely
  let sessionKey = 'Unknown';
  try {
    if (isConnected) {
      const client = getClient();
      sessionKey = client.getSessionKey();
    }
  } catch (e) {
    // Client might not be ready
  }

  // Auto-focus when connected
  useEffect(() => {
    if (isConnected && textareaRef.current) {
      textareaRef.current.focus();
    }
  }, [isConnected]);

  const handleInputChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    setInput(e.target.value);
    adjustTextareaHeight();
  };

  const adjustTextareaHeight = () => {
    const textarea = textareaRef.current;
    if (textarea) {
      textarea.style.height = 'auto';
      textarea.style.height = `${Math.min(textarea.scrollHeight, 200)}px`;
    }
  };

  const handleKeyDown = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSend();
    }
  };

  const handleSend = async () => {
    if (!input.trim() || isSending || !isConnected) return;
    
    const message = input.trim();
    setInput('');
    if (textareaRef.current) {
      textareaRef.current.style.height = 'auto';
    }
    
    await onSend(message);
  };

  const handleAbort = async () => {
    try {
      const client = getClient();
      if (client) {
        await client.abortChat();
      }
    } catch (e) {
      console.error('Failed to abort chat:', e);
    }
  };

  return (
    <div className="border-t border-gray-200 bg-white p-4">
      <div className="mx-auto max-w-4xl">
        {connectionState.status === 'error' && (
          <div className="mb-2 text-center text-xs text-red-500">
            {connectionState.error || 'Connection lost'}
          </div>
        )}
        
        {/* Session Info Debug */}
        <div className="mb-2 text-xs text-gray-400 font-mono text-center">
           Session: {sessionKey}
        </div>

        <div className="flex items-end gap-2">
          <div className="relative flex-1">
            <textarea
              ref={textareaRef}
              value={input}
              onChange={handleInputChange}
              onKeyDown={handleKeyDown}
              placeholder={isConnected ? "Type a message..." : "Connect to start chatting..."}
              disabled={!isConnected || isSending}
              rows={1}
              className="chat-input min-h-[44px] max-h-[200px] pr-12"
              style={{ resize: 'none' }}
            />
            <div className="absolute bottom-2 right-2 text-[10px] text-gray-400 hidden sm:block">
              {input.length > 0 && (
                <span>Enter to send, Shift+Enter for new line</span>
              )}
            </div>
          </div>
          
          {isThinking ? (
            <button
              onClick={handleAbort}
              className="btn-secondary h-[44px] w-[44px] shrink-0 p-0"
              title="Stop generating"
            >
              <Square className="h-4 w-4 fill-current" />
            </button>
          ) : (
            <button
              onClick={handleSend}
              disabled={!isConnected || !input.trim() || isSending}
              className={cn(
                "btn-primary h-[44px] w-[44px] shrink-0 p-0 transition-all",
                !input.trim() && "opacity-50"
              )}
              title="Send message"
            >
              {isSending ? (
                <Loader2 className="h-4 w-4 animate-spin" />
              ) : (
                <Send className="h-4 w-4" />
              )}
            </button>
          )}
        </div>
      </div>
    </div>
  );
}
