import React, { createContext, useContext, useState, useCallback, useEffect } from 'react';
import type { GatewayConfig, ConnectionState, Message } from '../types';
import { OpenClawClient, getClient, resetClient } from '../client';
import { loadFromLocalStorage, saveToLocalStorage } from '../utils';
import { gatewayDocsReader } from '../gatewayDocsReader';

interface ConnectionContextType {
  client: OpenClawClient | null;
  connectionState: ConnectionState;
  messages: Message[];
  config: GatewayConfig | null;
  connect: (config: GatewayConfig) => Promise<void>;
  disconnect: () => void;
  sendMessage: (content: string) => Promise<void>;
  isConnecting: boolean;
  gatewayDocsMd?: string | null;
}

const ConnectionContext = createContext<ConnectionContextType | null>(null);

export function ConnectionProvider({ children }: { children: React.ReactNode }) {
  const [client, setClient] = useState<OpenClawClient | null>(null);
  const [connectionState, setConnectionState] = useState<ConnectionState>({
    status: 'disconnected'
  });
  const [messages, setMessages] = useState<Message[]>(() => loadFromLocalStorage<Message[]>('openclaw_messages', []));
  const [gatewayDocsMd, setGatewayDocsMd] = useState<string | null>(null);

  const [config, setConfig] = useState<GatewayConfig | null>(() => {
    // Priority 1: URL query params
    const params = new URLSearchParams(window.location.search);
    const gatewayUrl = params.get('gatewayUrl');
    const token = params.get('token');

    if (gatewayUrl) {
      const newConfig = {
        url: gatewayUrl,
        token: token || undefined
      };
      saveToLocalStorage('openclaw_config', newConfig);
      // Clean up URL
      const newUrl = window.location.pathname + window.location.hash;
      window.history.replaceState({}, '', newUrl);
      return newConfig;
    }

    // Priority 2: localStorage
    const saved = loadFromLocalStorage<GatewayConfig | null>('openclaw_config', null);
    if (saved) {
      console.log('[ConnectionContext] Loading saved config:', saved.url);
      return saved;
    }

    // Default: LAN access from 192.168.0.112
    return {
      url: 'ws://192.168.0.112:18789',
      token: '' // Leave empty to prompt user (more secure)
    };
  });

  // Save messages to localStorage when they change
  useEffect(() => {
    console.log('[ConnectionContext] Saving messages to localStorage:', messages.length);
    if (messages.length > 0) {
      saveToLocalStorage('openclaw_messages', messages);
    }
  }, [messages.length]); // 
  const [isConnecting, setIsConnecting] = useState(false);
  const connectionAttemptRef = React.useRef(0);

  const handleMessage = useCallback((message: Message) => {
    console.log('[DEBUG] UI handleMessage:', message);
    setMessages(prev => {
      const existingIndex = prev.findIndex(m => m.id === message.id);
      if (existingIndex >= 0) {
        const updated = [...prev];
        const existing = updated[existingIndex];

        // 
        if (existing.role === message.role) {
          // 
          if (message.content.startsWith(existing.content)) {
            updated[existingIndex] = {
              ...existing,
              content: message.content,
              status: 'sent'
            };
          } else {
            // chunk 
            updated[existingIndex] = {
              ...existing,
              content: existing.content + message.content,
              status: 'sent'
            };
          }
          return updated;
        }
      }
      return [...prev, { ...message, status: 'sent' }];
    });
  }, []);

  const handleConnectionChange = useCallback((state: ConnectionState) => {
    setConnectionState(state);
    //  getHistory connect 
  }, []);

  const connect = useCallback(async (newConfig: GatewayConfig) => {
    const attemptId = ++connectionAttemptRef.current;
    setIsConnecting(true);
    console.log(`[ConnectionContext] Starting connection attempt ${attemptId} to:`, newConfig.url);

    try {
      resetClient();
      const newClient = getClient(newConfig);

      newClient.onMessage = handleMessage;
      newClient.onStatusChange = (state) => {
        if (connectionAttemptRef.current === attemptId) {
          handleConnectionChange(state);
        }
      };

      await newClient.connect();

      if (connectionAttemptRef.current !== attemptId) {
        console.log(`[ConnectionContext] Connection attempt ${attemptId} superseded, closing...`);
        newClient.disconnect();
        return;
      }

      setClient(newClient);
      setConfig(newConfig);
      saveToLocalStorage('openclaw_config', newConfig);

      // Fetch gateway docs
      try {
        const docs = await gatewayDocsReader.fetchDocs();
        if (connectionAttemptRef.current === attemptId) {
          setGatewayDocsMd(docs);
        }
      } catch {
        // ignore doc fetch errors
      }

      // 
      const currentSession = newClient.getSessionKey();
      console.log(`[ConnectionContext] Attempt ${attemptId} connected! Loading history:`, currentSession);

      try {
        const history = await newClient.getHistory();
        if (connectionAttemptRef.current === attemptId) {
          console.log(`[ConnectionContext] History loaded for attempt ${attemptId}, count:`, history.length);
          setMessages(prev => {
            if (history.length === 0) return prev;
            return history;
          });
        }
      } catch (historyErr) {
        console.error(`[ConnectionContext] Failed to load history for attempt ${attemptId}:`, historyErr);
      }
    } catch (err) {
      if (connectionAttemptRef.current === attemptId) {
        console.error(`[ConnectionContext] Connection attempt ${attemptId} failed:`, err);
        setConnectionState({
          status: 'error',
          error: err instanceof Error ? err.message : String(err) || 'Connection failed'
        });
      }
    } finally {
      if (connectionAttemptRef.current === attemptId) {
        setIsConnecting(false);
      }
    }
  }, [handleMessage, handleConnectionChange]);

  const disconnect = useCallback(() => {
    resetClient();
    setClient(null);
    setConnectionState({ status: 'disconnected' });
    // Keep messages for persistence unless explicitly cleared
    // setMessages([]);
  }, []);

  // Auto-reconnect logic for stability (e.g., SSH tunnel drops)
  useEffect(() => {
    let reconnectTimer: any;

    // Only auto-reconnect if:
    // 1. We were connected (client exists)
    // 2. We are now disconnected (not error, not connecting)
    // 3. We have a config
    if (connectionState.status === 'disconnected' && config && client && !isConnecting) {
      console.log('[ConnectionContext] Connection lost. Attempting auto-reconnect in 3s...');
      reconnectTimer = setTimeout(() => {
        console.log('[ConnectionContext] Auto-reconnecting...');
        connect(config);
      }, 3000);
    }

    return () => {
      if (reconnectTimer) clearTimeout(reconnectTimer);
    };
  }, [connectionState.status, config, client, connect, isConnecting, messages]); // Add messages dependency to persist across reconnects

  const sendMessage = useCallback(async (content: string) => {
    if (!client || !client.isConnected()) {
      throw new Error('Not connected to gateway');
    }

    // Create user message with idempotency key
    const idempotencyKey = `send_${Date.now()}_${content.substring(0, 20)}`;

    const userMessage: Message = {
      id: `msg_${Date.now()}`,
      role: 'user',
      content,
      timestamp: Date.now(),
      status: 'sending',
      metadata: {
        idempotencyKey
      } as any
    };

    setMessages(prev => [...prev, userMessage]);

    try {
      const { idempotencyKey: returnedKey } = await client.sendMessage(content);

      // Update message status by matching idempotency key
      setMessages(prev => prev.map(m => {
        const meta = m.metadata as any;
        const metaIdempotencyKey = meta?.idempotencyKey;
        if (metaIdempotencyKey === returnedKey && m.status === 'sending') {
          return { ...m, status: 'sent' };
        }
        return m;
      }));
    } catch (error) {
      // Mark message as error
      setMessages(prev => prev.map(m => {
        const meta = m.metadata as any;
        const metaIdempotencyKey = meta?.idempotencyKey;
        if (metaIdempotencyKey === idempotencyKey && m.status === 'sending') {
          return { ...m, status: 'error' };
        }
        return m;
      }));
      throw error;
    }
  }, [client]);

  // Auto-connect on load if config exists
  useEffect(() => {
    let isMounted = true;

    const doAutoConnect = async () => {
      //  disconnected 
      //  config 
      if (config && !client && !isConnecting && connectionState.status === 'disconnected') {
        console.log('[ConnectionContext] Auto-connecting with saved config:', config.url);

        try {
          // 
          await new Promise(resolve => setTimeout(resolve, 500));
          if (!isMounted) return;

          await connect(config);
          if (isMounted) console.log('[ConnectionContext] Auto-connect successful');
        } catch (err) {
          if (isMounted) {
            console.error('[ConnectionContext] Auto-connect failed:', err);
            //  error  connect 
          }
        }
      }
    };

    doAutoConnect();

    return () => {
      isMounted = false;
    };
  }, [config, client, isConnecting, connectionState.status, connect]); // Add dependencies

  // No auto-connect to prevent loops
  useEffect(() => {
    // Initial load only, don't auto-connect
  }, []);

  return (
    <ConnectionContext.Provider
      value={{
        client,
        connectionState,
        messages,
        config,
        connect,
        disconnect,
        sendMessage,
        isConnecting,
        gatewayDocsMd
      }}
    >
      {children}
    </ConnectionContext.Provider>
  );
}

export function useConnection() {
  const context = useContext(ConnectionContext);
  if (!context) {
    throw new Error('useConnection must be used within a ConnectionProvider');
  }
  return context;
}
